}

/* The sidebar menu */
.sidebar {
    text-align: center;
    border-radius: 25px;
    height: 550px;
    width: 300px;
    /* change this with JavaScript */
    position: fixed;
    z-index: 1;
    top: 347px;
    left: 300px;
    background-color: #a2d4d0;
    overflow-x: hidden;
    transition: 0.5s;
}

/* The sidebar links */
.sidebar a {
    text-decoration: none;
    font-size: 25px;
    color: #818181;
    display: block;
    transition: 0.3s;
}

.sidebar .closebtn {
    position: absolute;
    top: 286px;
    right: 5px;
    margin-left: 50px;
    background-image: url(./assets/categories/open_close.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    width: 35px;
    height: 35px;
    z-index: 1;
}

.sidebar .openbtn {
    position: absolute;
    top: 286px;
    right: 5px;
    margin-left: 50px;
    background-image: url(./assets/categories/open_close.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    width: 35px;
    height: 35px; /* padding: 8px 8px 8px 32px; */
    z-index: 1;
    /* open */
    -moz-transform: scaleX(-1);
    -o-transform: scaleX(-1);
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
    filter: FlipH;
    -ms-filter: 'FlipH';
}

/* goods group button */
.btn-good-group {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 5px;
    margin-left: 5px;
}

.btn-good {
    margin-left: 10px;
    margin-right: 10px;
}
/* Style page content - use this if you want to push the page content to the right when you open the side navigation */
#main {
    transition: margin-left 0.5s;
    /* If you want a transition effect */
    padding: 20px;
}

/* categories */
#categories-body {
}
.row-categories {
    display: flex;
    flex-wrap: wrap;
}
.category {
    /* sidebar width: 300px/2 */
    height: 122px;
    border: 1px solid #92c2be;
    transition: all 150ms linear;
}
.category-title {
    color: #005dbb;
}
.category-image:hover,
.sidebar .closebtn:hover,
.sidebar .openbtn:hover {
    cursor: pointer;
}
.category:hover {
    background-color: #b4dad7;
}
.category-image {
    transition: all 150ms linear;
}
.category-image:hover {
    transform: scale(1.1);
}

/* cards - goods */
/* card preview: images */
#preview {
    object-fit: cover;
}
.img-center {
    margin-right: auto;
    margin-left: auto;
}

/* Media Query */
@media only screen and (max-width: 800px) {
    .bg2 {
        display: none;
    }

    .navbar .navbar-brand {
        font-size: 25px;
    }

    .jumbotron h1 {
        font-size: 30px;
    }

    .jumbotron button {
        font-size: 15px;
    }

    .btn-primary {
        width: 250px;
        height: 50px;
        font-weight: bold;
    }

    #formb {
        margin-left: 15%;
    }
    .formb {
        margin-left: 15%;
    }
    .signincard {
        width: 20rem;

        margin-left: 5%;
    }
    .inputs {
        width: 18rem;
    }
    .bg3 {
        display: none;
    }
}

@media only screen and (max-width: 600px) {
    .bg2 {
        display: none;
    }

    .navbar .navbar-brand {
        font-size: 25px;
        text-align: center;
    }

    .main-brand {
        margin-left: 5rem;
    }

    .jumbotron h1 {
        font-size: 30px;
    }

    .jumbotron button {
        font-size: 15px;
    }

    .cards {
        margin: 0 5% !important;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .container {
        margin-bottom: 7rem;
    }

    .footer-priv {
        margin-left: 6rem;
    }

    .fa,
    .fa-envelope-square {
        margin-left: 8rem !important;
    }

    .fa,
    .fa-github {
        margin-left: 5rem;
    }
    #formb {
        margin-left: 15%;
    }
    .formb {
        margin-left: 15%;
    }
}

